import React, { Component } from 'react';
// import logo from './logo.svg';
import './App.css';
import { HomeInfo } from './home.js'
import Navbar from './NavBar.js';
import { BrowserRouter, Switch, Route, Link, Redirect, NavLink } from 'react-router-dom';
import { SearchPage } from './search.js'
import { MusicPage } from './music.js'

class App extends Component {
  render() {
    return (
      <div className="App" aria-label="main app component">
        {/*common nav bar across sub menus*/}
        <Navbar ></Navbar>
        {/*change route from home to search to topmusic*/}
        <Switch>
          <Route path={process.env.PUBLIC_URL + "/search"} component={SearchPage} />
          <Route path="/topmusic" component={MusicPage} />
          <Route path="/" component={HomeInfo} />
        </Switch>
        <footer class="container-fluid text-center" aria-label="footer information for the website">
          <p>Designed by Kishore Vasan and Robert Kim. Copyrights 2018. </p>
        </footer>

      </div>
    );
  }
}



export default App;
